   </v-btn>\r\n                    \r\n                </v-toolbar-items>\r\n                <v-toolbar-title style=\"color:white\">AÃ±adir grupo</v-toolbar-title>\r\n                <v-spacer></v-spacer>\r\n                <v-toolbar-items>\r\n                        <v-btn\r\n                        dark\r\n                        text\r\n                        @click=\"dialog = false\"\r\n                        >\r\n                        \r\n                        </v-btn>\r\n                    </v-toolbar-items>\r\n                 </v-toolbar>\r\n                 <v-card-text>\r\n                    \r\n    \r\n                    <v-row class=\"mt-2\">        \r\n                        <v-col>\r\n                            <div class=\"pl-4 pr-4\" style=\" height: 40px; \">\r\n                                <v-autocomplete\r\n                                    v-model=\"oficinasSelecionadas\"\r\n                                    clearable\r\n                                    class=\"mt-0 pt-0\"\r\n                                    dense\r\n                                    label=\"Oficinas\"\r\n                                    outlined\r\n                                    :items=\"oficinas\"\r\n                                    :filter=\"customFilterOficina\"\r\n                                    item-value=\"iduoper\"\r\n                                    item-text=\"nombre\"\r\n                                    :search-input.sync=\"oficinas_search\"\r\n                                    required\r\n                                    multiple\r\n                                >\r\n                                    <template v-slot:selection=\"{ item, index }\">\r\n                                        <v-chip v-if=\"index === 0\">\r\n                                            <span>{{ item.nombre }}</span>\r\n                                        </v-chip>\r\n                                        <span\r\n                                        v-if=\"index === 1\"\r\n                                        class=\"grey--text text-caption\"\r\n                                        >\r\n                                        (+{{ oficinasSelecionadas.length - 1  }} others)\r\n                                        </span>\r\n                                    </template>\r\n                                    <template v-slot:no-data>\r\n                                        <v-list-item>\r\n                                            <v-list-item-title>\r\n                                                <template>\r\n                                                    No hay registros en el inventario\r\n                                                </template>\r\n                                            </v-list-item-title>\r\n                                        </v-list-item>\r\n                                    </template>\r\n    \r\n                                    <template v-slot:item=\"data\">\r\n                                        <v-list-item-content>\r\n                                            <v-list-item-title v-html=\"data.item.iduoper\">\r\n                                            </v-list-item-title>\r\n                                            <v-list-item-subtitle>\r\n                                                {{ data.item.nombre }}\r\n                                            </v-list-item-subtitle>\r\n                                        </v-list-item-content>\r\n                                    </template>\r\n                                </v-autocomplete>\r\n                            </div>\r\n                            <div class=\"mt-2\"></div>\r\n    \r\n                            <div class=\"pl-4 pr-4\" style=\" height: 40px; \">\r\n                                <v-autocomplete\r\n                                    v-model=\"usuariosSelecionadas\"\r\n                                    clearable\r\n                                    class=\"mt-0 pt-0\"\r\n                                    dense\r\n                                    label=\"Persona\"\r\n                                    outlined\r\n                                    :items=\"usuarios\"\r\n                                    :filter=\"customFilterUsuario\"\r\n                                    item-value=\"id\"\r\n                                    item-text=\"nombre\"\r\n                                    :search-input.sync=\"usuarios_search\"\r\n                                    required\r\n                                    multiple\r\n                                >\r\n                                    <template v-slot:selection=\"{ item, index }\">\r\n                                        <v-chip v-if=\"index === 0\">\r\n                                            <span>{{ item.nombres }} {{item.paterno}} {{ item.materno}} </span>\r\n                                        </v-chip>\r\n                                        <span\r\n                                        v-if=\"index === 1\"\r\n                                        class=\"grey--text text-caption\"\r\n                                        >\r\n                                        (+{{ usuariosSelecionadas.length - 1  }} others)\r\n                                        </span>\r\n                                    </template>\r\n                                    <template v-slot:no-data>\r\n                                        <v-list-item>\r\n                                            <v-list-item-title>\r\n                                                <template>\r\n                                                    No hay registros en el inventario\r\n                                                </template>\r\n                                            </v-list-item-title>\r\n                                        </v-list-item>\r\n                                    </template>\