          </v-card-text>\r\n                                                </v-card>\r\n                                            </div>\r\n                                            <div class=\"pa-7 pr-4 pt-2\">\r\n                                                <span style=\"color:#000000; font-size: 1rem; \">Oficinas Selecionadas</span>\r\n                                                <v-card elevation=\"0\"  style=\"border: solid 0.5px #cdcdcd; min-height: 200px;\">\r\n                                                    <!-- <v-card-title><span style=\"font-size:1rem;\"> Areas Selecionadas</span></v-card-title> -->\r\n                                                    <v-card-text>\r\n                                                        <div v-for=\"item in oficinasSelecionadas\" :key=\"item.id\">\r\n                                                            <span class=\"mdi mdi-label-outline\"></span> {{ buscaOficinabyID(item) }}\r\n                                                        </div>\r\n                                                    </v-card-text>\r\n                                                </v-card>\r\n                                            </div>    \r\n                                        </div>\r\n                                        <div>\r\n                                            \r\n                                        </div>\r\n    \r\n                                    </div>\r\n                                </v-row>    \r\n                                \r\n                            <v-card-actions>\r\n                            <v-btn\r\n                                text\r\n                                @click=\"limpiar\"\r\n                            >\r\n                                Reset\r\n                            </v-btn>\r\n    \r\n                            <v-spacer ></v-spacer>\r\n    \r\n                            <v-btn\r\n                                class=\"white--text\" \r\n                                color=\"primary darken-1\"\r\n                                depressed\r\n                                @click=\"Guardar\"\r\n                            >\r\n                                Guardar\r\n                                <v-icon right>\r\n                                mdi-content-save\r\n                                </v-icon>\r\n                            </v-btn>\r\n                            </v-card-actions>\r\n                             \r\n                        </v-col>\r\n    \r\n                    </v-row>\r\n    \r\n                 </v-card-text>\r\n            </v-card>\r\n            </v-dialog>\r\n        </div>\r\n    \r\n    </v-container>\r\n    </template>\r\n    <script>\r\n    import Layout from \"@/Layouts/AdminLayout\";\r\n    import axios from 'axios';\r\n    import AreasByOficinaGrupo from './Components/AreasByOfcinaGrupo.vue';\r\n    \r\n    \r\n    export default {\r\n        components:{AreasByOficinaGrupo},\r\n    \r\n        layout: Layout,\r\n        data: () => ({\r\n            tree: [],\r\n            types: [],\r\n            items:[],\r\n            open: [1, 2],\r\n            search: null,\r\n            caseSensitive: false,\r\n            usuarios:[],\r\n            oficinas:[],\r\n            usuariosSelecionadas:null,\r\n            oficinasSelecionadas:null,\r\n            usuarios_search:\"\",\r\n            oficinas_search:\"\",\r\n            search:\"\",\r\n            dialog: false,\r\n            \r\n    \r\n            snackbar: false,\r\n            text: '',\r\n            timeout: 2000,\r\n            \r\n    \r\n            tab: null,\r\n            tabs: [ 'Oficinas/Areas', 'Usuarios'],\r\n            text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',\r\n        \r\n            expanded: [],   \r\n            dessertHeaders: [\r\n              {\r\n                text: '',\r\n                align: 'start',\r\n                sortable: false,\r\n                value: 'nombre',\r\n              },\r\n              { text: 'name', value: 'data-table-expand' },\r\n            ],\r\n        }),\r\n    \r\n        computed: {\r\n          filter () {\r\n            return this.caseSensitive\r\n              ? (item, search, textKey) => item[textKey].indexOf(search) > -1\r\n              : undefined\r\n          },\r\n        },\r\n        methods:{\r\n            \r\n            async getItemsGroup() {\r\n                let res = await axios.get(\"/admin/grupo/items-group\");\r\n                this.items = res.data.datos;\r\n                return res.data.datos.data;\r\n            },\r\n            \r\n            async getUsuarios() {\r\n                let res = await axios.get(\"/admin/usuarios/getUsuariosAll\");\r\n                this.usuarios = res.data.datos;\r\n                return res.data.datos.data;\r\n            },\r\n    \r\n    \r\n            async getOficinas() {\r\n                let res = await axios.get(\"/admin/oficinas/getallOficinasG\");\r\n                this.oficinas = res.data.datos;\r\n                return res.data.datos.data;\r\n            },\r\n    \r\n            // async getOficinas() {\r\n            //     let res = await axios.get(\"/admin/oficina/o\");\r\n            //     this.oficinas = res.data.datos;\r\n            //     return res.data.datos.data;\r\n            // },\r\n    \r\n    \r\n            customFilterUsuario(item, queryText, itemText) {\r\n                const nombres = item.nombres.toLowerCase();\r\n                //const dni = item.dni.toLowerCase();\r\n                const searchText = queryText.toLowerCase();\r\n                return (\r\n                    nombres.indexOf(searchText) > -1 //||\r\n                    //dni.indexOf(searchText) > -1\r\n                 );\r\n            },\r\n    \r\n            customFilterOficina(item, queryText, itemText) {\r\n                const nombre = item.nombre.toLowerCase();\r\n                const iduoper = item.iduoper.toLowerCase();\r\n                const searchText = queryText.toLowerCase();\r\n                return (\r\n                    nombre.indexOf(searchText) > -1 ||\r\n                    iduoper.indexOf(searchText) > -1\r\n                 );\r\n            },\r\n    \r\n            limpiar(){\r\n                this.tree = [];\r\n                this.usuariosSelecionadas = [];\r\n                this.oficinasSelecionadas = [];\r\n            },\r\n    \r\n            \r\n            buscaPersonabyID(id){\r\n                for(let i in this.usuarios){\r\n                    if (this.usuarios[i].id === id ){\r\n                        return (this.usuarios[i].nombres+\" \"+this.usuarios[i].paterno + \" \" + this.usuarios[i].materno ) \r\n                    }\r\n                }\r\n            },\r\n    \r\n            buscaOficinabyID(id){\r\n                for(let i in this.oficinas){\r\n                    if (this.oficinas[i].iduoper === id ){\r\n                        return (this.oficinas[i].nombre ) \r\n                    }\r\n                }\r\n            },\r\n    \r\n            async Guardar() {\r\n                let res = await axios.post(\r\n                    \"/admin/grupo/guardar\",{\r\n                    usuarios: this.usuariosSelecionadas,\r\n                    ofici: this.oficinasSelecionadas\r\n                    }\r\n                );\r\n                this.dialog = false;\r\n                this.text = \"Grupo Creado\";\r\n                this.snackbar = true;\r\n            },\r\n            clickColumn(slotData) {\r\n                const indexRow = slotData.index;\r\n                const indexExpanded = this.expanded.findIndex(i => i === slotData.item);\r\n                if (indexExpanded > -1) {\r\n                this.expanded.splice(indexExpanded, 1)\r\n                } else {\r\n                this.expanded.push(slotData.item);\r\n                }\r\n            },\r\n        },\r\n        created() {\r\n            // this.getItemsGroup()\r\n            this.getUsuarios()\r\n            this.getOficinas()\r\n        },\r\n    \r\n    };\r\n    </script>\r\n    \r\n    <style scoped>\r\n    .treee{\r\n        background: none;\r\n    }\r\n    .treee::-webkit-scrollbar {\r\n        -webkit-appearance: none;\r\n    }\r\n    \r\n    .treee::-webkit-scrollbar:vertical {\r\n        width:10px;\r\n    }\r\n    \r\n    .treee::-webkit-scrollbar-button:increment,.treee::-webkit-scrollbar-button {\r\n        display: none;\r\n    } \r\n    \r\n    .treee::-webkit-scrollbar:horizontal {\r\n        height: 10px;\r\n    }\r\n    \r\n    .treee::-webkit-scrollbar-thumb {\r\n        background-color: #797979;\r\n        border-radius: 20px;\r\n        border: 2px solid #f1f2f3;\r\n    }\r\n    \r\n    .treee::-webkit-scrollbar-track {\r\n        border-radius: 10px;  \r\n    }\r\n    .v-data-table_expanded.v-data-tableexpanded_content {\r\n      box-shadow: none !important;\r\n    }\r\n    </style>"],"sourceRoot":""}]);
